@import "../../css/share.scss";
.dapp-buy {
    padding: px(60) 0;
    box-sizing: border-box;
    background: #171932;
    font-family: PingFang SC;
    &__tips {
        width: px(690);
        padding: px(36) px(30) px(48);
        box-sizing: border-box;
        margin: 0 auto;
        border-radius: px(16);
        background: #24243e;
        h3 {
            color: #e2e2e2;
            font-size: px(30);
            padding-left: px(59);
            background: url($imgUrl + "/icon_tips.png") no-repeat 0 center;
            background-size: px(36) px(36);
        }
        p {
            color: #949496;
            font-size: px(28);
            line-height: 1.6;
            margin: 0;
        }
    }
    &__timeCount {
        font-weight: 500;
        text-align: center;
        h2 {
            font-size: px(40);
            color: #ffbe82;
        }
        p {
            font-size: px(26);
            color: #ffffff;
        }
    }
    &__form {
        padding: 0 px(30);
        margin-top: px(80);
        .form-item {
            margin-bottom: px(54);
            &__label {
                display: block;
                width: 100%;
                color: #ffffff;
                font-size: px(36);
                margin-bottom: px(34);
            }
            &__input {
                height: px(92);
                border-radius: px(8);
                overflow: hidden;
                input {
                    display: block;
                    width: 100%;
                    border: none;
                    outline: 0;
                    padding: px(30);
                    box-sizing: border-box;
                    color: #ffffff;
                    font-size: px(32);
                    background: #24243e;
                     ::-webkit-input-placeholder {
                        color: #a4a2a2;
                        font-size: px(32);
                    }
                     :-moz-placeholder {
                        /* Firefox 18- */
                        color: #a4a2a2;
                        font-size: px(32);
                    }
                     ::-moz-placeholder {
                        /* Firefox 19+ */
                        color: #a4a2a2;
                        font-size: px(32);
                    }
                     :-ms-input-placeholder {
                        color: #a4a2a2;
                        font-size: px(32);
                    }
                }
            }
            .error-msg {
                display: none;
                width: 100%;
                color: #dc6868;
                font-size: px(24);
                margin-top: px(6);
                &.show {
                    display: block;
                }
            }
        }
    }
    .btn-buy {
        display: block;
        width: px(676);
        height: px(92);
        font-size: px(32);
        color: #ffff;
        border: none;
        margin: px(100) auto 0;
        border-radius: px(10);
        background: linear-gradient(270deg, #c48656, #efbe8c);
        &.dsb {
            background: #c7c7c7;
            color: #ffffff;
        }
    }
}